/* eslint-disable no-restricted-imports */
import React, {useEffect} from 'react';
import $ from 'jquery';
import PropTypes from 'prop-types';
import 'datatables.net-bs4';
import {
    Portlet,
    PortletBody,
    PortletHeader,
  } from "../../partials/content/Portlet";
import Grid from '@material-ui/core/Grid';
import { makeStyles, withStyles  } from '@material-ui/core/styles';
import clsx from 'clsx';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepConnector from '@material-ui/core/StepConnector';
import Check from '@material-ui/icons/Check';
import AssignmentIcon from '@material-ui/icons/Assignment';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import DoneAllIcon from '@material-ui/icons/DoneAll';

const useStyles = makeStyles(theme => ({
    textField: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
      width: '100%',
    },
    dense: {
      marginTop: 19,
    },
}));

const ColorlibConnector = withStyles({
    alternativeLabel: {
      top: 35,
    },
    active: {
      '& $line': {
        backgroundImage:
          'linear-gradient( 95deg,#153264 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
      },
    },
    completed: {
      '& $line': {
        backgroundImage:
          'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,#153264 100%)',
      },
    },
    line: {
      height: 3,
      border: 0,
      backgroundColor: '#eaeaf0',
      borderRadius: 1,
    },
  })(StepConnector);

const useColorlibStepIconStyles = makeStyles({
    root: {
      backgroundColor: '#ccc',
      zIndex: 1,
      color: '#fff',
      width: 70,
      height: 70,
      display: 'flex',
      borderRadius: '50%',
      justifyContent: 'center',
      alignItems: 'center',
    },
    active: {
      backgroundImage:
        'linear-gradient( 136deg, #153264 0%, #153264 50%, #153264 100%)',
      boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)',
    },
    completed: {
      backgroundImage:
        'linear-gradient( 136deg, #183c7a 0%, #183c7a 50%, #183c7a 100%)',
    },
  });
  
  function ColorlibStepIcon(props) {
    const classes = useColorlibStepIconStyles();
    const { active, completed } = props;
  
    const icons = {
      1: <AssignmentIcon fontSize="large" color="secondary"/>,
      2: <AssignmentTurnedInIcon fontSize="large"/>,
      3: <DoneAllIcon fontSize="large"/>,
    };
  
    return (
      <div
        className={clsx(classes.root, {
          [classes.active]: active,
          [classes.completed]: completed,
        })}
      >
        {completed ? <Check className={classes.completed} fontSize="large"/> : icons[String(props.icon)]}
        
      </div>
    );
  }
  
  ColorlibStepIcon.propTypes = {
    active: PropTypes.bool,
    completed: PropTypes.bool,
    icon: PropTypes.node,
  };

function getSteps() {
    return ['Menunggu Persetujuan', 'Voucher Diapprove', 'Voucher Sudah Lunas'];
  }

export default function VoucherVerifikasi() {

    // eslint-disable-next-line no-unused-vars
    const classes = useStyles();
    // eslint-disable-next-line no-unused-vars
    const [activeStep, setActiveStep] = React.useState(0);
    const steps = getSteps();

     useEffect(() => {
        $(document).ready(function () {            
              $('#mytable').DataTable({
                        "searching": false,
                        "lengthChange": false,
                        "pagingType": "full_numbers",
                        "processing": true,
                        "serverSide": true,
                        "destroy": true,
                        "order": [[ 0, "desc" ],  [ 1, 'desc' ]],
                        "ajax": {
                          "url": "https://prd2.betaaplikasi.com:8888/prod/utils/api/v1/address/provinsi?length=34",
                          "type": 'get',
                        },
                        "columns": [
                            { "data": "mprv_id" },
                            { "data": "mprv_name" },
                            { "data": "mprv_id" },
                            { "data": "mprv_name" },
                            { "data": "mprv_id" },
                            { "data": "mprv_name" },
                        ]
                    });
                    $('.dataTables_filter,.dataTables_paginate').addClass('pull-right')
                })
     })
    return(
        <>              
            <Portlet>
                <PortletHeader
                    title="Step Voucher"
                >
                </PortletHeader>
                <PortletBody>
                    <Grid container spacing={3}>
                        <Stepper alternativeLabel activeStep={activeStep} connector={<ColorlibConnector />} style={{width:'100%'}}>
                            {steps.map(label => (
                            <Step key={label}>
                                <StepLabel StepIconComponent={ColorlibStepIcon}>{label}</StepLabel>
                            </Step>
                            ))}
                        </Stepper>
                    </Grid>
                </PortletBody>
            </Portlet>
            <Portlet>
                <PortletBody>
                    <table className="table" id="mytable" style={{width:'100%'}}>
                            <thead className="bg-primary" style={{color:'white'}}>
                                <tr>
                                <th scope="col">ID</th>
                                <th scope="col">Provinsi</th>
                                <th scope="col">ID</th>
                                <th scope="col">Provinsi</th>
                                <th scope="col">ID</th>
                                <th scope="col">Provinsi</th>
                                </tr>
                            </thead>
                    </table>
                </PortletBody>
            </Portlet>
        </>
    )
}